import React, { Component } from 'react'
import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';
import './details.css'


const Details = (props) => {
   
    return (

        <div>
            <img src={props.location.state}/>
            <div className='cancell' onClick={()=>{
                props.history.push('/home');
                
            }}>
                <span className="cancelTab">{'<'}</span>
            </div>
            <div className='bot'>
                <div className="botImg">
                    <i style={{color:'red'}}className='iconfont icondianpu'></i>
                    <span>店铺</span>
                </div>
                <div className="botImg">
                    <i className='iconfont iconkefu'></i>
                    <span>客服</span>
                </div>
                <div className="botImg">
                    <i className='iconfont iconshoucang'></i>
                    <span>收藏</span>
                </div>
                <div className='botbtn' id='add'>加入购物车</div>
                <div className='botbtn' id='buy'>立即购买</div>
            </div>
        </div>

    );
  }


export default Details