import React from 'react'
import {Button,TabBar} from 'antd-mobile';
import {withRouter} from 'react-router-dom';
import "./mytab.css"

class Mytab extends React.Component{
	constructor(props){
		super(props);
		this.state={
			pathname:'home'

		};

	}
	
	render() {
        const {pathname}=this.props.location;
		return (        
			<div style={{
				position: 'fixed', 
				width: '100%', 
				bottom: 0 ,
				backgroundColor:'rgb(238, 233, 233)',
			}}>
                 

			<TabBar
			  unselectedTintColor="#949494"
			  //tintColor="red"
			  barTintColor="white"
			>
			  <TabBar.Item
				title="首页"
				key="home"
				icon={
					<i className='iconfont iconHomehomepagemenu'></i>                           
				}

				selectedIcon={ 
					<i className='iconfont iconHomehomepagemenu1'></i>    
				}
				selected={pathname === '/home'}
				onPress={() => {
                  this.props.history.push('/home');
				}}
			  >
			  </TabBar.Item>

			  <TabBar.Item
			  	title="购物车"
				key="cart"
				icon={
					<i className='iconfont icongou_wu_che2'></i> 
				}
				selectedIcon={
					<i className='iconfont icongouwuche2'></i> 
				}
				selected={pathname === '/cart'}
				onPress={() => {
				  this.props.history.push('/cart')
				}}
			  >
			  </TabBar.Item>

			  <TabBar.Item
				title="订单列表"
				key="list"
				icon={
					<i className='iconfont icondingdan'></i>
				}
				selectedIcon={
					<i className='iconfont iconiconfontdingdan'></i>
				}			
				selected={pathname === '/order'}
				onPress={() => {
					this.props.history.push('/order')
				}}
			  >
			  </TabBar.Item>

			  <TabBar.Item
				title="我的淘宝"
				key="mine"
				icon={
				<i className='iconfont iconmine'></i>
			 }
			 	selectedIcon={
					<i className='iconfont iconmine-red'></i>
				 }
			 	
				selected={pathname === '/mine'}
				onPress={() => {
					this.props.history.push('/mine')
				}}
			  >

			  </TabBar.Item>


			</TabBar>
		  </div>
		);
	  }
	}
	



export default withRouter(Mytab);
